import Vue from "vue";
import Router from "vue-router";
import NProgress from "nprogress";
import Apply from "./views/Apply";
import ApplicationStatus from "./views/ApplicationStatus";
import TicketOfferFromApplication from "./views/TicketOfferFromApplication";
import TicketDetails from "./views/TicketDetails";
import SpacesFull from "./views/SpacesFull";
import TicketOfferPartner from "./views/TicketOfferPartner";
import TicketOfferPurchased from "./views/TicketOfferPurchased";
import TicketOfferSurrendered from "./views/TicketOfferSurrendered";
import TicketOfferExpired from "./views/TicketOfferExpired";
import NotFound from "./views/NotFound";

import TicketService from "@/services/TicketService";
Vue.use(Router);

const router = new Router({
  // mode: "history",
  base: "/events/05-2019/ticketing/",
  routes: [
    {
      path: "/apply",
      name: "apply",
      component: Apply
    },
    {
      path: "/apply/:id",
      name: "application-status",
      component: ApplicationStatus,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getApplication(routeTo.params.id)
          .then(response => {
            const application = response.data.application;
            const event = response.data.event;
            const ticketOffer = response.data.ticketOffer;
            if (application.status === "pending") {
              routeTo.params.applicant = response.data.applicant;
              routeTo.params.application = application;
              routeTo.params.ticketOffer = ticketOffer;
              routeTo.params.event = event;
              next();
            } else {
              next({
                name: "ticket-offer-from-application",
                params: { id: ticketOffer.id }
              });
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/update-ticket-info/:id",
      name: "update-ticket-info",
      component: TicketDetails,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            const ticketOffer = response.data.ticketOffer;
            const event = response.data.event;
            routeTo.params.personInit = response.data.recipient;
            routeTo.params.ticketOffer = ticketOffer;
            routeTo.params.event = event;
            next();
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/apply/offer/spaces-full",
      name: "spaces-full",
      component: SpacesFull,
      props: true
    },
    {
      path: "/apply/offer/:id",
      name: "ticket-offer-from-application",
      component: TicketOfferFromApplication,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            const ticketOffer = response.data.ticketOffer;
            const event = response.data.event;
            const person = response.data.recipient;
            if (event.soldOut) {
              next({
                name: "spaces-full",
                params: { personName: person.firstName }
              });
            } else if (ticketOffer.status === "void") {
              next({
                name: "ticket-offer-from-application",
                params: { id: ticketOffer.newTicketOfferId }
              });
            } else if (ticketOffer.status === "purchased") {
              next({
                name: "ticket-offer-purchased",
                params: { id: routeTo.params.id }
              });
            } else if (ticketOffer.status === "surrendered") {
              next({
                name: "ticket-offer-surrendered",
                params: { id: routeTo.params.id }
              });
            } else if (ticketOffer.status === "expiration_passed") {
              next({
                name: "ticket-offer-expired",
                params: { id: routeTo.params.id }
              });
            } else {
              routeTo.params.personInit = response.data.recipient;
              routeTo.params.ticketOffer = ticketOffer;
              routeTo.params.event = event;
              next();
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/ticket-offer/:id",
      name: "ticket-offer-partner",
      component: TicketOfferPartner,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            const ticketOffer = response.data.ticketOffer;

            if (ticketOffer.status === "purchased") {
              next({
                name: "partner-ticket-offer-purchased",
                params: { id: routeTo.params.id }
              });
            } else if (ticketOffer.status === "surrendered") {
              next({
                name: "partner-ticket-offer-surrendered",
                params: { id: routeTo.params.id }
              });
            } else if (ticketOffer.status === "expiration_passed") {
              next({
                name: "partner-ticket-offer-expired",
                params: { id: routeTo.params.id }
              });
            } else {
              routeTo.params.personInit = response.data.recipient;
              routeTo.params.ticketOffer = ticketOffer;
              routeTo.params.event = response.data.event;
              next();
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/ticket-offer/:id/purchased",
      name: "partner-ticket-offer-purchased",
      component: TicketOfferPurchased,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            const ticketOffer = response.data.ticketOffer;
            if (ticketOffer.status === "purchased") {
              routeTo.params.recipient = response.data.recipient;
              next();
            } else {
              next({
                name: "ticket-offer-partner",
                params: { id: routeTo.params.id }
              });
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/ticket-offer/:id/surrendered",
      name: "partner-ticket-offer-surrendered",
      component: TicketOfferSurrendered,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            const ticketOffer = response.data.ticketOffer;
            if (ticketOffer.status === "surrendered") {
              routeTo.params.recipient = response.data.recipient;
              next();
            } else {
              next({
                name: "ticket-offer-from-application",
                params: { id: routeTo.params.id }
              });
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/ticket-offer/:id/expired",
      name: "partner-ticket-offer-expired",
      component: TicketOfferExpired,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            const ticketOffer = response.data.ticketOffer;
            if (ticketOffer.status === "expiration_passed") {
              routeTo.params.recipient = response.data.recipient;
              next();
            } else {
              next({
                name: "ticket-offer-from-application",
                params: { id: routeTo.params.id }
              });
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/apply/offer/:id/purchased",
      name: "ticket-offer-purchased",
      component: TicketOfferPurchased,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            const ticketOffer = response.data.ticketOffer;
            if (ticketOffer.status === "purchased") {
              routeTo.params.recipient = response.data.recipient;
              next();
            } else {
              next({
                name: "ticket-offer-from-application",
                params: { id: routeTo.params.id }
              });
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/apply/offer/:id/surrendered",
      name: "ticket-offer-surrendered",
      component: TicketOfferSurrendered,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            const ticketOffer = response.data.ticketOffer;
            if (ticketOffer.status === "surrendered") {
              routeTo.params.recipient = response.data.recipient;
              next();
            } else {
              next({
                name: "ticket-offer-from-application",
                params: { id: routeTo.params.id }
              });
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/apply/offer/:id/expired",
      name: "ticket-offer-expired",
      component: TicketOfferSurrendered,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            const ticketOffer = response.data.ticketOffer;
            if (ticketOffer.status === "expiration_passed") {
              routeTo.params.recipient = response.data.recipient;
              next();
            } else {
              next({
                name: "ticket-offer-from-application",
                params: { id: routeTo.params.id }
              });
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/404",
      name: "404",
      component: NotFound
    },
    {
      path: "*",
      redirect: { name: "404" }
    }
  ]
});

router.beforeEach((routeTo, routeFrom, next) => {
  NProgress.start();
  next();
});

router.afterEach(() => {
  NProgress.done();
});

export default router;
