<template>
  <div class="section">
    <section class="container section-spacing">
      <div class="columns is-centered">
        <div class="column is-8">
          <h1 class="title is-1">Final Ticket Offer</h1>
          <h1 class="subtitle is-2">May 2019</h1>
        </div>
      </div>
    </section>

    <section class="container section-spacing content">
      <div class="columns is-centered">
        <div class="column is-7">
          <p class="is-size-4">
            Hi
            <span class="has-text-primary">{{ personName }}</span>
          </p>

          <p>
            So it's now first come, first served. There are
            <strong class="has-text-primary">{{ ticketsLeft }}</strong> tickets
            left, please complete the form below and buy your ticket now.
          </p>

          <p>
            Tickets are just <strong class="has-text-primary">$139</strong> to
            be part of this amazing event. You'll get to hear 5 awesome
            speakers, mix with like minded people, chat with the speakers
            themselves and to indulge in a four course meal prepared by one of
            New Zealand's pre-eminent chefs -
            <a class="link" href="https://benbayly.co.nz/" target="_blank"
              >Ben Bayly</a
            >.
          </p>

          <p>
            If you'd like to be at TEDxAuckland at Whoa! Studios next Wednesday
            then you'll need to act quickly. When they are gone, they are gone!
          </p>

          <p>
            <strong class="has-text-primary">
              The TEDxAuckland team
            </strong>
          </p>
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-6">
          <form @submit.prevent="acceptTicket">
            <h2 class="title is-3">Your Contact Info</h2>
            <PersonInfo class="section-spacing" :person="person" :$v="$v" />

            <h2 class="title is-3">General Questions</h2>
            <ApplicationQuestions
              class="section-spacing"
              :details="applicationQuestions"
              :$v="$v"
            />

            <h2 class="title is-3">Questions for Ticket</h2>
            <TicketQuestions
              class="section-spacing"
              :questions="ticketQuestions"
              :$v="$v"
            />

            <div class="buttons has-addons is-right">
              <input
                class="button is-primary is-medium"
                type="submit"
                value="Purchase through iTICKET"
              />
            </div>
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import PersonInfo from "@/components/PersonInfo";
import ApplicationQuestions from "@/components/ApplicationQuestions";
import TicketQuestions from "@/components/TicketQuestions.vue";
import TicketService from "@/services/TicketService";
import NProgress from "nprogress";
import moment from "moment";
import { personNameMixin } from "../mixins/personNameMixin";
import {
  personQuestionsMixin,
  personQuestionsValidations,
  applicationQuestionsMixin,
  applicationQuestionsValidations,
  ticketQuestionsMixin,
  ticketQuestionsValidations
} from "@/mixins/questionsMixin";

export default {
  name: "TicketOfferFromApplication",
  components: {
    PersonInfo,
    ApplicationQuestions,
    TicketQuestions
  },
  props: {
    personInit: {
      type: Object,
      required: true
    },
    ticketOffer: {
      type: Object,
      required: true
    },
    event: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      isRejectModalActive: false
    };
  },
  mixins: [
    personQuestionsMixin,
    applicationQuestionsMixin,
    ticketQuestionsMixin,
    personNameMixin
  ],
  computed: {
    formattedTicketPrice() {
      return `$${this.ticketOffer.costAfterDiscountCents / 100}`;
    },
    expirationDate() {
      return moment(this.ticketOffer.iticket.expiration);
    },
    formattedExpirationDate() {
      return this.expirationDate.format("h:mm:ss a [on the] Do MMMM");
    },
    timeLeft() {
      return this.expirationDate.fromNow(true);
    },
    ticketsLeft() {
      const lowTicketsNumber = 18;
      if (this.event.standardTicketsLeft < lowTicketsNumber) {
        return this.event.standardTicketsLeft;
      } else {
        return lowTicketsNumber;
      }
    }
  },
  validations: {
    person: personQuestionsValidations,
    applicationQuestions: applicationQuestionsValidations,
    ticketQuestions: ticketQuestionsValidations
  },
  methods: {
    acceptTicket() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();
        TicketService.acceptTicket({
          id: this.ticketOffer.id,
          contactQuestions: this.person,
          ticketQuestions: {
            ...this.applicationQuestions,
            ...this.ticketQuestions
          }
        })
          .then(response => {
            NProgress.done();
            window.location.assign(response.data.purchase_url);
          })
          .catch(e => {
            NProgress.done();
            console.log("ERROR", e);
          });
      }
    }
  }
};
</script>

<style>
.link {
  text-decoration: underline;
}
</style>
